<template>
    <div class="row">
        <div class="col-12 col-lg-6">
            <div ref="canvas"
                 class="canvas"
                 :style="canvas_style"
                 @mousedown="clickdown"
                 @touchstart="clickdown"
                 @mouseup="clickup"
                 @mouseleave="clickup"
                 @touchend="clickup"
                 @mousemove="dragMove"
                 @touchmove="dragMove"
            >
                <div ref="ball" class="ball" :style="ball_style"></div>
                <div class="launch_line" :style="launch_line_style(ll_num)" v-for="ll_num in 3"></div>
            </div>
        </div>
        <div class="col-12 col-lg-6">
            <table>
                <tr><td>tickrate:</td><td><input type="range" v-model="tickrate" min="1" max="100" /></td><td>{{tickrate}}</td></tr>
                <tr><td>gravity:</td><td><input type="range" v-model="gravity" min="1" max="50" /></td><td>{{gravity}}</td></tr>
                <tr><td>volume density:</td><td><input type="range" v-model="density" min="0" max="15" /></td><td>{{density}}</td></tr>
                <tr><td>bounce force:</td><td><input type="range" v-model="bounce_force" min="0" max="11" /></td><td>{{bounce_force}}</td></tr>
                <tr><td>canvas width:</td><td><input type="range" v-model="canvas.w" :min="ball.w * 2" max="500" /></td><td>{{canvas.w}}</td></tr>
                <tr><td>canvas height:</td><td><input type="range" v-model="canvas.h" :min="ball.h * 2" max="500" /></td><td>{{canvas.h}}</td></tr>
            </table>
            <table>
                <tr><td>x</td><td>{{ball.x}}</td></tr>
                <tr><td>y</td><td>{{ball.y}}</td></tr>
                <tr><td>speed x</td><td>{{ball.sx}}</td></tr>
                <tr><td>speed y</td><td>{{ball.sy}}</td></tr>
            </table>
        </div>
    </div>
</template>

<script>
    export default {
        name: "main",
        data: function () {
            return {
                drag: {
                    on: false,
                    startPosX: 0,
                    startPosY: 0,
                    startTime: 0,
                    x: 0,
                    y: 0,
                },
                ball: {
                    x: 100,
                    y: 100,
                    w: 50,
                    h: 50,
                    sx: 0,
                    sy: 0,
                },
                canvas: {
                    w: 500,
                    h: 500
                },
                tickrate: 60,
                gravity: 10,
                density: 1,
                bounce_force: 9,
                launch_line_ball_diameter: 10,
            };
        },
        methods: {
            tick() {
                if (!this.drag.on) {
                    let r = this.ball.h / 2;
                    let density_koef = Math.min(1, (100 - this.density) / 100);
                    let bounce_koef = this.bounce_force / 10;
                    let gravity_acc = this.gravity / this.tickrate;
                    if (this.ball.y < this.canvas.h - r) {
                        this.ball.sy += gravity_acc;
                    }
                    if (this.ball.y - r <= 0 && this.ball.sy < 0 ||
                        this.ball.y + r >= this.canvas.h && this.ball.sy > 0) {
                        this.ball.sy *= -bounce_koef;
                    }
                    if (this.ball.x - r <= 0 && this.ball.sx < 0 ||
                        this.ball.x + r >= this.canvas.w && this.ball.sx > 0) {
                        this.ball.sx *= -bounce_koef;
                    }
                    this.ball.sy *= density_koef;
                    this.ball.sx *= density_koef;

                    this.ball.sy = this.round(this.ball.sy, 5);
                    this.ball.sx = this.round(this.ball.sx, 5);
                    this.ball.y = this.round(Math.min(this.canvas.h - r, Math.max(r, this.ball.sy + this.ball.y)), 3);
                    this.ball.x = this.round(Math.min(this.canvas.w - r, Math.max(r, this.ball.sx + this.ball.x)), 3);
                }
                setTimeout(this.tick, 1000 / this.tickrate);
            },
            clickdown(ev) {
                if (!this.drag.on) {
                    this.ball.sx = 0;
                    this.ball.sy = 0;
                    this.drag.on = true;
                    let coords = this.getPointerCoordinates(ev, this.$refs.canvas);
                    this.drag.startPosX = coords.x;
                    this.drag.startPosY = coords.y;
                    this.drag.x = coords.x;
                    this.drag.y = coords.y;
                    this.ball.x = coords.x;
                    this.ball.y = coords.y;
                    ev.preventDefault();
                }
            },
            clickup(ev) {
                if (this.drag.on) {
                    this.drag.on = false;
                    this.ball.sx = Math.floor(this.drag.x - this.drag.startPosX);
                    this.ball.sy = Math.floor(this.drag.y - this.drag.startPosY);
                    ev.preventDefault();
                }
            },
            dragMove(ev) {
                if (this.drag.on) {
                    let coords = this.getPointerCoordinates(ev, this.$refs.canvas);
                    // this.ball.x = coords.x;
                    // this.ball.y = coords.y;
                    this.drag.x = coords.x;
                    this.drag.y = coords.y;
                    ev.preventDefault();// disable page scroll on touch drag
                }
            },
            getPointerCoordinates(ev, element) {
                let rect = element.getBoundingClientRect();
                let x, y;
                if (ev.type.substring(0, 5) === 'touch') {
                    x = ev.touches[0].clientX;
                    y = ev.touches[0].clientY;
                } else {
                    x = ev.clientX;
                    y = ev.clientY;
                }
                return {
                    x: Math.round(x - rect.left),
                    y: Math.round(y - rect.top),
                }
            },
            launch_line_style(num) {
                let x, y;
                if (num === 1) {
                    x = this.drag.startPosX;
                    y = this.drag.startPosY;
                } else if (num === 2) {
                    x = this.drag.startPosX + (this.drag.x - this.drag.startPosX) / 2;
                    y = this.drag.startPosY + (this.drag.y - this.drag.startPosY) / 2;
                } else {
                    x = this.drag.x;
                    y = this.drag.y;
                }
                return {
                    display: this.drag.on ? 'block' : 'none',
                    top: (y - this.launch_line_ball_diameter / 2) + 'px',
                    left: (x - this.launch_line_ball_diameter / 2) + 'px',
                    width: this.launch_line_ball_diameter + 'px',
                    height: this.launch_line_ball_diameter + 'px',
                }
            },
            round(v, n) {
                n = n || 0;
                let k = 10 ** n;
                return Math.round(v * k) / k;
            }
        },
        computed: {
            ball_style() {
                return {
                    top: (this.ball.y - this.ball.h / 2) + 'px',
                    left: (this.ball.x - this.ball.w / 2) + 'px',
                    width: this.ball.w + 'px',
                    height: this.ball.h + 'px',
                }
            },
            canvas_style() {
                return {
                    width: this.canvas.w + 'px',
                    height: this.canvas.h + 'px',
                }
            },
        },
        mounted() {
            this.canvas.w = Math.min(this.canvas.w, window.innerWidth - 20);
            let _this = this;
            window.addEventListener('resize', () => {
                _this.canvas.w = Math.min(_this.canvas.w, window.innerWidth - 20);
            });
            this.tick();
        }
    }
</script>

<style scoped lang="scss">
    @import '~bootstrap/scss/bootstrap.scss';
    .canvas {
        position: relative;
        background-color: aliceblue;
    }

    .ball {
        background-color: red;
        border-radius: 50%;
        position: absolute;
    }

    .launch_line {
        position: absolute;
        border-radius: 50%;
        z-index: 10;
        background-color: blue;
    }
</style>